<template>
    <div class="address">
        <tab-box :top-menu="topMenu"></tab-box>
    </div>
</template>

<script>
    import addAddress from "../../components/home/addAddress.vue"
    import tabBox from "../../components/layout/web/tabBox";
    export default {
        name: "addressView",
        components :{
            tabBox,addAddress
        },
        data () {
            return {
                tableData:{
                    allNum:0
                }
            }
        },
        computed:{
            topMenu:{
                get(){
                    let menu={};
                    let langVal = [];
                    if(this.tableData && this.tableData.hasOwnProperty('allNum')){
                        langVal.push(this.tableData.allNum);
                    }else{
                        langVal.push(0);
                    }
                    langVal.push(this.allAddressNum);
                    menu.text = {
                        val:this.$L('已有{$1}个收货地址，最多创建20个',[this.tableData.allNum]),langVal,
                        border:true
                    };
                    menu.buttonList=[
                        {
                            text:this.$L('添加收货地址'),
                            func:this.addAddressFunc
                        }
                    ];
                    return menu;
                }
            }
        },
        methods : {
            addAddressFunc(){
                const _self = this;
                if(_self.tableData.allNum < 20){
                    _self.$store.commit('setDialogObj', {
                        name: this.$L('添加收货地址'),
                        view: addAddress,
                        width: '600px',
                    });
                } else {
                    _self.$message({
                        type: "error",
                        message: _self.$L("最多可添加20条地址信息!")
                    });
                }

            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('getAddressList',{} , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
        },
        created() {
            this.getData();
        }

    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/common.scss";
    .address{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
    }
</style>